*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  --soft-blue: hsl(215, 51%, 70%);
  --cyan: hsl(178, 100%, 50%);
  --light-cyan: hsl(178deg 100% 50% / 40%);
  --bg-dark-blue: hsl(217, 54%, 11%);
  --card-dark-blue: hsl(216, 50%, 16%);
  --line-dark-blue: hsl(215, 32%, 27%);
  --white: hsl(0, 0%, 100%);
  --main-transition: 0.3s ease-in-out;
}

html {
  font-size: 100%;
}

body {
  font-size: 1.125rem;
  font-family: "Outfit", sans-serif;
}

@media (max-width: 374px) {
  body {
    font-size: 0.875rem;
  }
}

img {
  max-width: 100%;
  display: block;
  background: no-repeat center center / cover;
}

a {
  text-decoration: none;
}

.main {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  width: 100%;
  padding: 50px 0; /*Extra space*/
  background-color: hsl(217, 54%, 11%);
  background-color: var(--bg-dark-blue);
}

/* Start Attribution Styles [NOT Related To Project Styles And Can Be Deleted] */
.footer {
  /* Reset Footer Bottom Padding*/
  padding-bottom: 0;
}

.attribution {
  color: #fff;
  font-size: 0.8125rem;
  text-align: center;
  padding-top: 1.875rem;
  padding-bottom: 1.5625rem;
  background-color: hsl(217, 54%, 11%);
  background-color: var(--bg-dark-blue);
}

.attribution .outer-link {
  color: #fff;
  margin-left: 0.0625rem;
  transition: all 0.2s ease-in-out;
}

.attribution .outer-link:hover {
  color: hsl(178, 100%, 50%);
  color: var(--cyan);
  cursor: pointer;
}

.attribution .social-media {
  list-style: none;
  margin-left: 0.3125rem;
  display: inline-flex;
  align-items: flex-end;
  grid-gap: 0.75rem;
  gap: 0.75rem;
}

.attribution .social-media img {
  width: 22px;
  height: 22px;
  transition: -webkit-transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}

.attribution .social-media img:hover {
  -webkit-transform: scale(1.08);
          transform: scale(1.08);
}

.attribution .social-media img.light {
  -webkit-filter: invert(1);
          filter: invert(1);
}

/* End Attribution */


